'use strict';

import './styles.scss';
import '@babel/polyfill';
import $ from 'jquery';
import moment from 'moment';
import humanize from 'humanize';
import videojs from 'video.js';
import 'videojs-errors';
import { version as videojsErrorsVersion } from 'videojs-errors/package.json';
import packageJson from '~root/package.json';
window.videojs = videojs;
window.CLSP_DEMO_VERSION = packageJson.version;
var defaultWallUrls = ['clsp://172.28.12.248/testpattern', 'clsp://172.28.12.247/testpattern', 'clsps://sky-qa-dionysus.qa.skyline.local/testpattern', 'clsp://172.28.12.57:9001/FairfaxVideo0520', 'clsp://172.28.12.57:9001/40004'];
var defaultTourUrls = [].concat(defaultWallUrls);
var wallInterval = null;
var tourInterval = null;
var wallPlayers = [];
var tourPlayers = [];

function destroyAllWallPlayers() {
  for (var i = 0; i < wallPlayers.length; i++) {
    var player = wallPlayers[i];
    player.dispose();
  }

  wallPlayers = [];
}

function destroyAllTourPlayers() {
  for (var i = 0; i < tourPlayers.length; i++) {
    var player = tourPlayers[i];
    player.dispose();
  }

  tourPlayers = [];
}

function initializeWall() {
  function createVideoPlayer(index, playerOptions) {
    var videoId = "wall-video-".concat(index);
    var $container = $('#videowall').append(document.getElementById('wall-video-template').innerHTML).find('#wall-container-null');
    var $video = $container.find('video');
    $video.attr('id', videoId);
    $container.attr('id', "wall-container-".concat(index));
    $container.find('.video-stream .index').text(index);
    $container.find('.video-stream .url').text(playerOptions.sources[0].src);
    $container.find('.video-stream .close').on('click', function () {
      $('#wallTotalVideos').text(parseInt($('#wallTotalVideos').text(), 10) - 1);
      player.dispose();
    });
    var $videoMetrics = $container.find('.wall-video-metrics');
    var metricTypes = [// ClspPlugin().METRIC_TYPES,
      // IOV.METRIC_TYPES,
      // Conduit.METRIC_TYPES,
      // IOVPlayer.METRIC_TYPES,
      // MediaSourceWrapper.METRIC_TYPES,
      // SourceBufferWrapper.METRIC_TYPES,
    ];

    for (var i = 0; i < metricTypes.length; i++) {
      var metricType = metricTypes[i];

      for (var j = 0; j < metricType.length; j++) {
        var text = metricType[j];
        var name = text.replace(new RegExp(/\./, 'g'), '-');
        var $metric = $('<div/>', {
          "class": "metric ".concat(name)
        });
        $metric.append($('<span/>', {
          "class": 'value'
        }));
        $metric.append($('<span/>', {
          "class": 'type',
          title: text,
          text: text
        }));
        $videoMetrics.append($metric);
      }
    }

    var player = window.videojs(videoId, playerOptions);
    player.on('dispose', function () {
      for (var _i = 0; _i < wallPlayers.length; _i++) {
        if (player === wallPlayers[_i]) {
          wallPlayers.splice(_i, 1);
        }
      }
    });
    wallPlayers.push(player);
    var tech = player.clsp();
    tech.on('metric', function (event, _ref) {
      var metric = _ref.metric;
      $videoMetrics.find(".".concat(metric.type.replace(new RegExp(/\./, 'g'), '-'), " .value")).attr('title', metric.value).html(metric.value);
    });
  }

  var $controls = $('.wall .controls');
  var $controlsToggle = $('#wall-controls-toggle');

  function toggleControls() {
    $controlsToggle.attr('data-state') === 'hidden' ? showControls() : hideControls();
  }

  function showControls() {
    $controls.show();
    $controlsToggle.attr('data-state', 'shown');
    $controlsToggle.text('Hide Controls');
  }

  function hideControls() {
    $controls.hide();
    $controlsToggle.attr('data-state', 'hidden');
    $controlsToggle.text('Show Controls');
  }

  function setMetricsVisibility() {
    if ($('#wallShowMetrics').prop('checked')) {
      $('.wall-video-metrics').show();
    } else {
      $('.wall-video-metrics').hide();
    }
  }

  function onclick() {
    destroyAllWallPlayers();
    var urlList = window.localStorage.getItem('skyline.clspPlugin.wallUrls').split('\n');
    var timesToReplicate = window.localStorage.getItem('skyline.clspPlugin.wallReplicate');
    var videoIndex = 0;

    for (var i = 0; i < timesToReplicate; i++) {
      for (var j = 0; j < urlList.length; j++) {
        var playerOptions = {
          autoplay: true,
          muted: true,
          preload: 'auto',
          poster: '../skyline_logo.png',
          controls: true,
          sources: [{
            src: urlList[j % urlList.length],
            type: "video/mp4; codecs='avc1.42E01E'"
          }],
          clsp: {
            enableMetrics: $('#wallEnableMetrics').prop('checked')
          }
        };
        createVideoPlayer(videoIndex, playerOptions);
        videoIndex++;
      }
    }

    var now = Date.now();
    document.getElementById('videowall').style.gridTemplateColumns = "repeat(".concat(Math.ceil(Math.sqrt(videoIndex + 1)), ", 1fr)");
    $('#wallTotalVideos').text(videoIndex);
    $('#wallStartTime').text(moment(now).format('MMMM Do YYYY, h:mm:ss a'));

    if (wallInterval) {
      window.clearInterval(wallInterval);
    }

    $('#wallDuration').text('0 hours 0 minutes 0 seconds');
    wallInterval = setInterval(function () {
      var hoursFromStart = Math.floor(moment.duration(Date.now() - now).asHours());
      var minutesFromStart = Math.floor(moment.duration(Date.now() - now).asMinutes()) - hoursFromStart * 60;
      var secondsFromStart = Math.floor(moment.duration(Date.now() - now).asSeconds()) - hoursFromStart * 60 * 60 - minutesFromStart * 60;
      $('#wallDuration').text("".concat(hoursFromStart, " hours ").concat(minutesFromStart, " minutes ").concat(secondsFromStart, " seconds"));

      if (window.performance && window.performance.memory) {
        $('#wallHeapSizeLimit').text(humanize.filesize(window.performance.memory.jsHeapSizeLimit));
        $('#wallTotalHeapSize').text(humanize.filesize(window.performance.memory.totalJSHeapSize));
        $('#wallUsedHeapSize').text(humanize.filesize(window.performance.memory.usedJSHeapSize));
      }
    }, 1000);
    hideControls();
    setMetricsVisibility();
  }

  $('#wallCreate').click(onclick);
  $('#wall-controls-toggle').click(toggleControls);
  $('#wallShowMetrics').on('change', setMetricsVisibility);
  var $wallUrls = $('#wallUrls');

  if (!window.localStorage.getItem('skyline.clspPlugin.wallUrls')) {
    window.localStorage.setItem('skyline.clspPlugin.wallUrls', defaultWallUrls.join('\n'));
  }

  $wallUrls.val(window.localStorage.getItem('skyline.clspPlugin.wallUrls'));
  $wallUrls.on('change', function () {
    window.localStorage.setItem('skyline.clspPlugin.wallUrls', $wallUrls.val().trim());
  });
  var $wallReplicate = $('#wallReplicate');

  if (!window.localStorage.getItem('skyline.clspPlugin.wallReplicate')) {
    window.localStorage.setItem('skyline.clspPlugin.wallReplicate', 1);
  }

  $wallReplicate.val(window.localStorage.getItem('skyline.clspPlugin.wallReplicate'));
  $wallReplicate.on('change', function () {
    window.localStorage.setItem('skyline.clspPlugin.wallReplicate', $wallReplicate.val().trim());
  });
}

function initializeTour() {
  function createVideoPlayer(index, playerOptions) {
    var videoId = "tour-video-".concat(index);
    var $container = $('#tourwall').append(document.getElementById('tour-video-template').innerHTML).find('#tour-container-null');
    var $video = $container.find('video');
    $video.attr('id', videoId);
    $container.attr('id', "tour-container-".concat(index));
    $container.find('.video-stream .index').text(index);
    $container.find('.video-stream .url').text(playerOptions.sources[0].src);
    $container.find('.video-stream .close').on('click', function () {
      $('#tourTotalVideos').text(parseInt($('#tourTotalVideos').text(), 10) - 1);
      player.dispose();
    });
    var $videoMetrics = $container.find('.tour-video-metrics');
    var metricTypes = [// ClspPlugin().METRIC_TYPES,
      // IOV.METRIC_TYPES,
      // Conduit.METRIC_TYPES,
      // IOVPlayer.METRIC_TYPES,
      // MediaSourceWrapper.METRIC_TYPES,
      // SourceBufferWrapper.METRIC_TYPES,
    ];

    for (var i = 0; i < metricTypes.length; i++) {
      var metricType = metricTypes[i];

      for (var j = 0; j < metricType.length; j++) {
        var text = metricType[j];
        var name = text.replace(new RegExp(/\./, 'g'), '-');
        var $metric = $('<div/>', {
          "class": "metric ".concat(name)
        });
        $metric.append($('<span/>', {
          "class": 'value'
        }));
        $metric.append($('<span/>', {
          "class": 'type',
          title: text,
          text: text
        }));
        $videoMetrics.append($metric);
      }
    }

    var player = window.videojs(videoId, playerOptions);
    player.on('dispose', function () {
      for (var _i2 = 0; _i2 < wallPlayers.length; _i2++) {
        if (player === wallPlayers[_i2]) {
          wallPlayers.splice(_i2, 1);
        }
      }
    });
    tourPlayers.push(player);
    player.on('changesrc', function (event, source) {
      $container.find('.video-stream .url').text(source.src);
    });
    var tech = player.clsp();
    tech.on('metric', function (event, _ref2) {
      var metric = _ref2.metric;
      $videoMetrics.find(".".concat(metric.type.replace(new RegExp(/\./, 'g'), '-'), " .value")).attr('title', metric.value).html(metric.value);
    });
  }

  var $controls = $('.tour .controls');
  var $controlsToggle = $('#tour-controls-toggle');

  function toggleControls() {
    $controlsToggle.attr('data-state') === 'hidden' ? showControls() : hideControls();
  }

  function showControls() {
    $controls.show();
    $controlsToggle.attr('data-state', 'shown');
    $controlsToggle.text('Hide Controls');
  }

  function hideControls() {
    $controls.hide();
    $controlsToggle.attr('data-state', 'hidden');
    $controlsToggle.text('Show Controls');
  }

  function setMetricsVisibility() {
    var $tourMetrics = $('.tour-video-metrics');

    if ($('#tourShowMetrics').prop('checked')) {
      $tourMetrics.show();
    } else {
      $tourMetrics.hide();
    }
  }

  function onclick() {
    destroyAllTourPlayers();
    var urlList = window.localStorage.getItem('skyline.clspPlugin.tourUrls').split('\n');
    var timesToReplicate = window.localStorage.getItem('skyline.clspPlugin.tourReplicate');
    var tourDurationValue = window.localStorage.getItem('skyline.clspPlugin.tourDurationValue');
    var videoIndex = 0;

    for (var i = 0; i < timesToReplicate; i++) {
      var sources = [];
      var type = "video/mp4; codecs='avc1.42E01E'";

      for (var j = 0; j < urlList.length; j++) {
        sources.push({
          src: urlList[j],
          type: type
        });
      }

      var playerOptions = {
        autoplay: true,
        muted: true,
        preload: 'auto',
        poster: '../skyline_logo.png',
        controls: true,
        sources: sources,
        clsp: {
          tourDuration: tourDurationValue * 1000,
          enableMetrics: $('#tourEnableMetrics').prop('checked')
        }
      };
      createVideoPlayer(videoIndex, playerOptions);
      videoIndex++;
    }

    var now = Date.now();
    document.getElementById('tourwall').style.gridTemplateColumns = "repeat(".concat(Math.ceil(Math.sqrt(timesToReplicate)), ", 1fr)");
    document.getElementById('tourwall').style.gridTemplateRows = "repeat(".concat(Math.ceil(Math.sqrt(timesToReplicate)), ", 1fr)");
    $('#tourTotalVideos').text("".concat(videoIndex, " x ").concat(urlList.length, " (").concat(videoIndex * urlList.length, ")"));
    $('#tourStartTime').text(moment(now).format('MMMM Do YYYY, h:mm:ss a'));

    if (tourInterval) {
      window.clearInterval(tourInterval);
    }

    $('#tourDuration').text('0 hours 0 minutes 0 seconds');
    tourInterval = setInterval(function () {
      var hoursFromStart = Math.floor(moment.duration(Date.now() - now).asHours());
      var minutesFromStart = Math.floor(moment.duration(Date.now() - now).asMinutes()) - hoursFromStart * 60;
      var secondsFromStart = Math.floor(moment.duration(Date.now() - now).asSeconds()) - hoursFromStart * 60 * 60 - minutesFromStart * 60;
      $('#tourDuration').text("".concat(hoursFromStart, " hours ").concat(minutesFromStart, " minutes ").concat(secondsFromStart, " seconds"));

      if (window.performance && window.performance.memory) {
        $('#tourHeapSizeLimit').text(humanize.filesize(window.performance.memory.jsHeapSizeLimit));
        $('#tourTotalHeapSize').text(humanize.filesize(window.performance.memory.totalJSHeapSize));
        $('#tourUsedHeapSize').text(humanize.filesize(window.performance.memory.usedJSHeapSize));
      }
    }, 1000);
    hideControls();
    setMetricsVisibility();
  }

  $('#tourCreate').click(onclick);
  $('#tour-controls-toggle').click(toggleControls);
  $('#tourShowMetrics').on('change', setMetricsVisibility);
  var $tourUrls = $('#tourUrls');

  if (!window.localStorage.getItem('skyline.clspPlugin.tourUrls')) {
    window.localStorage.setItem('skyline.clspPlugin.tourUrls', defaultTourUrls.join('\n'));
  }

  $tourUrls.val(window.localStorage.getItem('skyline.clspPlugin.tourUrls'));
  $tourUrls.on('change', function () {
    window.localStorage.setItem('skyline.clspPlugin.tourUrls', $tourUrls.val().trim());
  });
  var $tourReplicate = $('#tourReplicate');

  if (!window.localStorage.getItem('skyline.clspPlugin.tourReplicate')) {
    window.localStorage.setItem('skyline.clspPlugin.tourReplicate', 12);
  }

  $tourReplicate.val(window.localStorage.getItem('skyline.clspPlugin.tourReplicate'));
  $tourReplicate.on('change', function () {
    window.localStorage.setItem('skyline.clspPlugin.tourReplicate', $tourReplicate.val().trim());
  });
  var $tourDurationValue = $('#tourDurationValue');

  if (!window.localStorage.getItem('skyline.clspPlugin.tourDurationValue')) {
    window.localStorage.setItem('skyline.clspPlugin.tourDurationValue', 10);
  }

  $tourDurationValue.val(window.localStorage.getItem('skyline.clspPlugin.tourDurationValue'));
  $tourDurationValue.on('change', function () {
    window.localStorage.setItem('skyline.clspPlugin.tourDurationValue', $tourDurationValue.val().trim());
  });
}

$(function () {
  var pageTitle = "CLSP ".concat(window.CLSP_DEMO_VERSION, " Demo Page");
  document.title = pageTitle;
  $('#page-title-version').html(window.CLSP_DEMO_VERSION);
  $('#page-title-videojs-version').html(window.videojs.VERSION);
  $('#page-title-videojs-error-version').html(videojsErrorsVersion);
  window.HELP_IMPROVE_VIDEOJS = false;
  initializeWall(); // initializeTour();
});